.sidebar {
    position: fixed; /* Используем fixed для наложения на другую страницу */
    top: 0;
    left: -300px; /* Начальное положение вне экрана */
    width: 300px;
    height: 100vh;
    background-color: #fff;
    border: 1px solid black;
    transition: left 0.3s ease; /* Плавный переход для анимации */
    z-index: 1000; /* Убедитесь, что панель выше других элементов */}

.sidebar.open {
    left: 0; /* Позиция, когда панель открыта */
}

.background {
    z-index: 500;
}

.nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-left: 10px;
    padding-top: 30px;
    font-size: 20px;
}

.nav a {
    padding: 10px 20px;
    text-decoration: none;
    color: black;
}

.nav a:hover {
    color: #58ACFF;

}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;

}


.close {
    position: absolute;
    top: 10px;
    left: 250px;
    z-index: 1001;
}

